
import wx from 'weixin-js-sdk'
import { mapActions ,mapGetters} from 'vuex'
import empty from '../components/empty'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import adrBtn from '../components/addressBtn'
export default {
name: "choice-address-view",
data(){
return {
}
},
computed:{
...mapGetters({
adress:'getadressLb'
})
},
created(){
document.title = '收货地址'


},
mounted(){
if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}

this.addressList({this:this,method:this.addressList})
},
methods:{
...mapActions(['addressList','addressUpdate']),
newAdre(){
this.$router.push(`/plusAddress?new=1&sign=${this.$route.query.sign||''}&reBack=${this.$route.query.reBack||''}`)
},
toBj(id){
this.$router.push({name:'plusAddress',query:{new:0,id:id,sign:this.$route.query.sign||'',reBack:this.$route.query.reBack||''}})
},
setMehod(){
this.hideLoading()
let _this = this
if(this.$route.query.sign){//vue页面过来选地址

}
else  if(this.$route.query.reBack){//小程序页面过来

wx.miniProgram.navigateBack({
delta:1
})
}
else{
this.showTost('已设置为默认地址')
this.addressList({this:this,method:this.addressList})
}
},
coseAder(obj){
this.showLoading()
this.addressUpdate({
data:{
address:obj.paAddress1,
tel:obj.paMobilePhone,
name:obj.paAttnName,
is_def:'Y',
contactMechId:obj.contactMechId,
countyGeoId:obj.paCountyGeoId,
stateProvinceGeoId:obj.paStateProvinceGeoId,
city:obj.paCity
},
this:this})
}
},
components:{
CheckIcon,adrBtn , empty
}
}



